<template>
  <div id="search">
    <Input @on-keydown.enter="search()" @on-click="search()" v-model="keyword" icon="ios-search" placeholder="输入书籍关键字" size="large" style="width: 100%;"></Input>
    <ul>
      <li v-for="book in result">
        <router-link :to="'/source/'+book._id">{{book.title}}&emsp;-{{book.author}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      keyword: "",
      result: [],
      list: ["%", "/", "@"]
    };
  },
  methods: {
    search() {
      //api1不允许跨域，使用proxyTable在dev下跨域，但build下失败
      //api2允许跨域
      var api1 = "/myapi/book/fuzzy-search?query=" + this.keyword;
      var api2 = "http://novel.juhe.im/search?keyword=" + this.keyword;
      this.$http.get(api2).then(
        data => {
          console.log(data);
          this.result = data.body.books;
        },
        err => {
          console.log(err);
        }
      );
    }
  }
};
</script>
<style lang="scss" scoped>
#search {
  width: 42%;
  margin: 40px auto;
}
ul {
  margin-top: 10px;

  li {
    list-style: none;
    margin: 2px 0;
    font-size: 14px;
  }
}
</style>




