<template>
  <div id="txt">
    {{title}}
    <div v-html="content"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      text: [],
      title:"",
      content: ""
    };
  },
  methods: {
    getText(cid) {
      var api1 = "/chapterapi/chapter/" + encodeURIComponent(cid);
      var api2 = "http://novel.juhe.im/chapters/" + encodeURIComponent(cid);
      this.$http.get(api2).then(
        data => {
          console.log(data);
          this.text = data.body.chapter;
          if(data.body.chapter.title.length>3){
            this.title=data.body.chapter.title
          }
          this.content =
            "<p>" + data.body.chapter.cpContent.replace(/\n/g, "</p><p>") + "</p>";
        },
        err => {
          console.log(err);
        }
      );
    }
  },
  mounted() {
    var chapterId = this.$route.params.cid; //获取动态路由传值
    this.getText(chapterId);
  }
};
</script>
<style lang="scss">
#txt {
  background: #bed0bf;
  margin: 0px auto;
  padding: 20px 8%;
  font-size: 18px;
  p {
    margin-bottom: 12px;
  }
}
</style>





