<template>
  <div id="chapter">
    <ul>
      <li v-for="item in ChapterList">
        <router-link :to="'/txt/'+item.link">{{item.title}}</router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ChapterList: []
    };
  },
  methods: {
    getChapter(sid) {
      var api1 = "/myapi/atoc/" + sid + "?view=chapters"
      var api2 = "http://novel.juhe.im/book-chapters/" + sid 
      this.$http.get(api2).then(
        data => {
          console.log(data);
          this.ChapterList = data.body.chapters;
          this.encode()
        },
        err => {
          console.log(err);
        }
      );
    },
    encode() {
      for (var i=0;i<this.ChapterList.length;i++) {
        this.ChapterList[i].link = encodeURIComponent(this.ChapterList[i].link)
      }
    }
  },
  mounted() {
    var sourceId = this.$route.params.sid; //获取动态路由传值
    this.getChapter(sourceId);
  }
};
</script>
<style lang="scss" scoped>
#chapter {
  margin: 20px 0px 0px 36%;
  font-size: 14px;
}
ul {
  margin-top: 10px;

  li {
    list-style: none;
    margin: 2px 0;
    font-size: 14px;
  }
}
</style>





