<template>
  <div id="source">
    <ul>
      <li v-for="item in sourceList">
        <router-link :to="'/chapter/'+item._id">来源：{{item.name}} &emsp;最新章节：{{item.lastChapter }}
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      sourceList: []
    };
  },
  methods: {
    getBookInfo(bid) {
      var api1 = "/myapi/atoc?view=summary&book=" + bid;
      var api2 = "http://novel.juhe.im/book-sources?view=summary&book=" + bid;
      this.$http.get(api2).then(
        data => {
          console.log(data);
          this.sourceList = data.body;
        },
        err => {
          console.log(err);
        }
      );
    }
  },
  mounted() {
    var bookId = this.$route.params.bid; //获取动态路由传值
    this.getBookInfo(bookId);
  }
};
</script>
<style lang="scss" scoped>
#source {
  width: 60%;
  margin: 20px auto;
  font-size: 14px;
}
ul {
  margin-top: 10px;

  li {
    list-style: none;
    margin: 2px 0;
    font-size: 14px;
  }
}
</style>





